import HitTitle from "./HitTitle";
import HintSpring from "./Animated/Spring/HintSpring";
import { useEffect, useRef, useContext } from "react";
import { useSelector } from "react-redux";
import { UnityContext } from "../content/GetFishUnity";

const FishingRodStep1 = () => {
  const { language, permissionGranted } = useSelector(
    (state) => state.connState
  );
  const {
    handleFishingRodWhipInAir,
    UnityCallMessage,
    handleFetchData,
    handleSwingTheRodStep,
  } = useContext(UnityContext);

  const { ,  } = language;

  const lastOrientation = useRef({ x: 0, y: 0, z: 0 });
  const lastAcceleration = useRef({ x: 0, y: 0, z: 0 });

  const speedThreshold = 10;

  const handleOrientation = (event) => {
    const x = Math.ceil(event.beta || 0);
    const y = Math.ceil(event.gamma || 0);
    const z = Math.ceil(event.alpha || 0);
    lastOrientation.current = { x, y, z };
  };
  const handleDevicemotion = (event) => {
    if (!event.accelerationIncludingGravity) return;
    const { x = 0, y = 0, z = 0 } = event.accelerationIncludingGravity;
    const { x: lastX, y: lastY, z: lastZ } = lastAcceleration.current;
    if (
      Math.abs(x - lastX) > speedThreshold ||
      Math.abs(y - lastY) > speedThreshold ||
      Math.abs(z - lastZ) > speedThreshold
    ) {
      OnClickDevicemotion();
    }

    lastAcceleration.current = { x, y, z };
  };

  function OnClickDevicemotion() {
    handleFishingRodWhipInAir();
    handleFetchData("", {
      x: lastOrientation.current.x,
      y: lastOrientation.current.y,
      z: lastOrientation.current.z,
    });
    UnityCallMessage("SetRodWait");
    vibrationMode();

    lastAcceleration.current = { x: 0, y: 0, z: 0 };
    lastOrientation.current = { x: 0, y: 0, z: 0 };
    handleSwingTheRodStep(2);
  }

  useEffect(() => {
    if (permissionGranted) {
      window.addEventListener("deviceorientation", handleOrientation);
      window.addEventListener("devicemotion", handleDevicemotion);

      return () => {
        window.removeEventListener("deviceorientation", handleOrientation);
        window.removeEventListener("devicemotion", handleDevicemotion);
      };
    }
  }, [permissionGranted]);

  return (
    <>
      {/* <button className="w-100" onClick={() => OnClickDevicemotion()}>
        
      </button> */}
      <HitTitle title={} />
      <HintSpring>{}</HintSpring>
    </>
  );
};

export default FishingRodStep1;
export function vibrationMode() {
  if ("vibrate" in navigator) {
    navigator.vibrate(1000);
  }
}
